.container {
  /* margin: 0 auto; */
  width: 800px;
  padding: 40px;
  position: relative;
}

.tittle {
  color: rgb(81, 81, 201);
  font-size: 40px;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  margin-bottom: 20px;
  border: 1px solid rgb(197, 195, 195);
  border-radius: 5px;
  box-shadow: 5px 2px 2px rgb(197, 195, 195);
}

.label {
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
}

.input {
  width: 250px;
  font-size: 16px;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid black;
}

.input:hover {
  border: 2px solid rgb(81, 81, 201);
}

.input:active {
  border: 2px solid rgb(81, 81, 201);
}

.button {
  width: 260px;
  height: 30px;
  margin-top: 35px;
  margin-bottom: 20px;
  color: white;
  font-size: 20px;
  background-color: rgb(81, 81, 201);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
